<template>
  <Chart :config="config"></Chart>
</template>

<script>
  import Chart from '@/components/screen/layer/base/Chart';
  import _ from 'lodash';

  export default {
    name: "BaseBar",
    props: ['config'],
    data() {
      return {}
    },
    created() {
      this.initOption();
    },
    methods: {
      /**
       * 初始化配置项
       */
      initOption() {
        let defaultOption = {
          backgroundColor: '#2F3943',
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'line',
              lineStyle: {
                opacity: 0
              }
            },
            formatter: function (prams) {
              return prams[0].data
            }
          },
          grid: {
            left: '0%',
            right: '0%',
            bottom: '5%',
            top: '7%',
            height: '85%',
            containLabel: true,
          },
          xAxis: [{
            type: 'category',
            gridIndex: 0,
            data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
            axisTick: {
              alignWithLabel: true
            },
            axisLine: {
              lineStyle: {
                color: '#0c3b71'
              }
            },
            axisLabel: {
              show: true,
              color: 'rgb(170,170,170)',
              fontSize: 16
            }
          }],
          yAxis: [{
            type: 'value',
            gridIndex: 0,
            splitLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            axisLine: {
              lineStyle: {
                color: '#0c3b71'
              }
            },
            axisLabel: {
              color: 'rgb(170,170,170)',
              formatter: '{value}'
            }
          }, {
            type: 'value',
            splitLine: {
              show: false
            },
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              show: false
            }
          }],
          series: [{
            name: '',
            type: 'bar',
            barWidth: '50%',
            xAxisIndex: 0,
            yAxisIndex: 0,
            label: {
              normal: {
                show: true,
                position: "top",
                textStyle: {
                  color: "#ffc72b",
                  fontSize: 16
                }
              }
            },
            itemStyle: {
              normal: {
                color: '#0286ff'
              }
            },
            data: [80, 87, 51, 81, 23, 45, 33],
            zlevel: 2
          }, {
            name: '背景',
            type: 'bar',
            barWidth: '50%',
            xAxisIndex: 0,
            yAxisIndex: 1,
            barGap: '-100%',
            data: [100, 100, 100, 100, 100, 100, 100],
            itemStyle: {
              normal: {
                color: 'rgba(255,255,255,0.1)'
              }
            },
            zlevel: 1
          }]
        };
        let result = _.merge(defaultOption, this.config.option);
        this.config.option = result;
      },
      handler() {
        let data = this.config.proxy.data;
        let mapper = this.config.proxy.mapper;
        let propX = mapper.x;
        let propY = mapper.y;
        let xAxis = [];
        let series = [];
        for (let i = 0; i < data.length; i++) {
          xAxis.push(data[i][propX]);
          series.push(data[i][propY]);
        }
        this.config.option.xAxis[0].data = xAxis
        this.config.option.series[0].data = series
      }
    },
    watch: {
      "config.option.series": {
        deep: true,
        handler: function(val) {
          this.config.option.series[1].barWidth = this.config.option.series[0].barWidth
        }
      }
    },
    components: {
      'Chart': Chart
    },
  }
</script>



