import Base from './Base'

class StaticData extends Base {
  constructor(config) {
    super(config);
  }

  do() {
    /**
     * Promise用于和其他类型的数据源保持相同，否则在DataProxy拿不到数据
     */
    return new Promise((resolve, reject) => {
      resolve(this.config.data)
    })
  }
}
export default StaticData



