<template>
  <div class="config-base">
    <el-tabs type="border-card" stretch>
      <el-tab-pane label="配置">
        <div class="title">{{getTitle}}</div>
        <component v-if="state.index !== undefined && state.index !== null"
                   :is="loader[config.components[state.index].id].default"
                   :component="config.components[state.index]"></component>
      </el-tab-pane>
      <el-tab-pane label="数据">
        <div class="title">{{getTitle}}</div>
        <Data :component="config.components[state.index]"></Data>
      </el-tab-pane>
      <el-tab-pane label="事件"></el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
  import Data from "./Data"

  export default {
    name: "Base",
    props: ["config", "state"],
    data() {
      return {
        // 动态加载组件的存储
        loader: {},
      }
    },
    created() {
      this.loadConfig()
    },
    computed: {
      getTitle() {
        return this.config.components[this.state.index] ? this.config.components[this.state.index].text : ""
      }
    },
    methods: {
      /**
       * 动态加载组件
       */
      loadConfig() {
        if (!this.config.components || !this.config.components.length) {
          return;
        }
        for (let i = 0; i < this.config.components.length; i++) {
          let comp = this.config.components[i]
          if (!comp || !comp.name || this.loader[comp.id]) {
            continue;
          }
          let path = "components/screen/config/" + comp.category + "/" + comp.name;
          this.loader[comp.id] = (require("../../../" + path + ".vue"));
        }
      },
    },
    watch: {
      "config.components": {
        immediate: true,
        handler: function (val, oldVal) {
          this.loadConfig();
        }
      }
    },
    components: {
      "Data": Data
    }
  }
</script>

<style scoped>

</style>



