<template>
  <div class="layer">
    <div class="layer-shift">
      <template v-for="(item, index) in shiftButton">
        <el-button type="text" @click="moveCard($event, index)">
          <i :class="'icon-' + shiftButton[index]" class="icon iconfont"></i>
        </el-button>
      </template>
    </div>
    <div class="layer-list">
      <template v-for="(cmp, index) in config.components">
        <div :class="state.index === index ? 'active' : ''"
             class="item"
             @click="activeCard($event, index)">
          <img :src="'../static/images/screen/components/' + cmp.category + '/' + cmp.name + '.png'">
          <span>{{cmp.text}}</span>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Layer",
    props: ["config", "state"],
    data() {
      return {
        shiftButton: ["top", "up", "down", "bottom"]
      }
    },
    methods: {
      /**
       * 选中一个组件卡片
       * @param index
       */
      activeCard(e, index) {
        // 阻止冒泡，防止全屏点击取消选中组件
        e.stopPropagation()
        if (this.state.index === index) {
          return
        }
        this.state.index = index
      },
      /**
       * 移动选中的组件卡片
       * @param index
       */
      moveCard(e, index) {
        e.stopPropagation()
        if (this.state.index === undefined || this.state.index === null) {
          return;
        }
        let item;
        switch (index) {
          case 0:
            if (this.state.index === 0) {
              return;
            }
            item = this.config.components[this.state.index];
            this.config.components.splice(this.state.index, 1)
            this.config.components.unshift(item);
            this.state.index = 0
            break;
          case 1:
            if (this.state.index === 0) {
              return;
            }
            item = this.config.components.slice(this.state.index - 1, this.state.index + 1).reverse();
            this.config.components.splice(this.state.index - 1, 2, item[0], item[1])
            this.state.index -= 1
            break;
          case 2:
            if (this.state.index === this.config.components.length) {
              return;
            }
            item = this.config.components.slice(this.state.index, this.state.index + 2).reverse();
            this.config.components.splice(this.state.index, 2, item[0], item[1])
            this.state.index += 1
            break;
          case 3:
            if (this.state.index === this.config.components.length) {
              return;
            }
            item = this.config.components[this.state.index];
            this.config.components.splice(this.state.index, 1);
            this.config.components.push(item);
            this.state.index = this.config.components.length - 1;
            break;
          default:
            break;
        }
      }
    }
  }
</script>

<style scoped>

</style>



