<template>
  <div v-if="!style || !style.hidden"
       class="layout"
       :class="style.class"
       :id="id">
    <!--<template v-for="(item, index) in maps">
      &lt;!&ndash; 装载地图，并将config参数传入 &ndash;&gt;
      <component :is="item.default"
                 :config="config.maps[index].config"></component>
    </template>-->
    <template v-for="(item, index) in plugins">
      <!-- 装载插件，并将config参数传入 -->
      <component :is="item.default"
                 :config="config.plugins[index].config"></component>
    </template>
    <template v-for="(item, index) in charts">
      <!-- 装载图表，并将config参数传入 -->
      <component :is="item.default"
                 :config="config.charts[index].config"></component>
    </template>
    <template v-for="(item, index) in tables">
      <!-- 装载表格，并将config参数传入 -->
      <component :is="item.default"
                 :config="config.tables[index].config"></component>
    </template>
    <template v-for="(item, index) in insights">
      <!-- 装载表格，并将config参数传入 -->
      <component :is="item.default"
                 :config="config.insights[index].config"></component>
    </template>
  </div>
</template>

<script>
  export default {
    name: "Layout",
    props: ['config'],
    data() {
      return {
        id: null,
        style: null,
        // maps: [],
        plugins: [],
        charts: [],
        tables: [],
        insights: [],
      }
    },
    created() {
      this.id = this.config.id || "layout-" + new Date().getTime();
      this.style = this.config.style || {class: ''}
      // this.handlerComponent(this.config.maps, "maps")
      this.handlerComponent(this.config.plugins, "plugins")
      this.handlerComponent(this.config.charts, "charts")
      this.handlerComponent(this.config.tables, "tables")
      this.handlerComponent(this.config.insights, "insights")
    },
    methods: {
      handlerComponent(comps, prop) {
        if (comps && comps.length) {
          comps.forEach(comp => {
            if (!comp || !comp.path || !comp.component) {
              return
            }
            this[prop].push(require("../../" + comp.path + ".vue"));
          });
        }
      }
    }
  }
</script>



