<!--
Plugin组件，通过传入:config，生成相应类型的元素
配置
{
  1. 样式
  style: {
    // 是否隐藏，默认false
    hidden: false,
    class: 样式class,
    position: {
      left: 0,
      top: 0,
      right: 'auto',
      bottom: 'auto',
      zIndex: 0,
      width: 'auto',
      height: 'auto'
    }
  }
  2. 远端获取数据代理
  proxy: {
    // 通过访问服务端获取数据
    remote: true,
    url: "",
    // POST、GET，默认POST
    method: "",
    // 参数
    param: null,
    // 缓存远端数据
    data: null
  },
  3. 数据绑定
  binder: {
    // name: 绑定规则命名
    // prop: 当前组件的属性路径，进行数据绑定的属性，必须在实例化后存在显式声明，
    // 例如：param.region，必须在config中配置 config: { param: {region: null} }
    // storeProp: Vuex中的属性路径
    // prop与storeProp进行单向数据绑定，当storeProp变化时，prop随之变化，
    // 并会触发当前组件的watch
    name:[prop, storeProp]
  },
  4. 代理函数
  handler: {
    // 数据转换过程，将远端获取的proxy.data转换到option，
    // 或者转换为chart对象其他属性的规则
    transformData: function(proxy.data, option, chart)
  },
  5. 事件
  event: {
    // 初始化完成
    onInit: (plugin) { },
    // 加载完成
    onLoad: (plugin) { },
  },

  6. 各类元素的配置参数：
  6.1. 按钮
  option: {
    type: 'button',
    style: 'plain-原始 / round-圆角 / circle-圆形',
    icon: '',
    html: '提交',
    disabled: false,
  },
  event: {
    onClick: (plugin) => { }
  },

  6.2. 按钮组
  option: {
    type: 'button-group',
    data: [
      {html: '上页', disabled: false},
      {html: '下页', disabled: true}
    ],
  },
  event: {
    onClick: (index, plugin) => { }
  },

  6.3. Radio
  option: {
    type: 'radio',
    style: 'button',
    value: null,
    name: '',
    data: [
      {value: 0, icon: 'icon-play', text: '选项1', disabled: false},
      {value: 1, icon: 'icon-play', text: '选项2', disabled: false},
    ],
  },
  event: {
    onChange: (label, index, option, plugin) => { }
  },

  6.4. Checkbox
  option: {
    type: 'check',
    // 样式，button / undefined
    style: 'button',
    name: '',
    value: [],
    data: [
      {value: 0, icon: 'icon-play', text: '选项1', checked: false, disabled: false},
      {value: 1, icon: 'icon-play', text: '选项2', checked: false, disabled: false},
    ],
  },
  event: {
    onChange: (value, index, option, plugin) => { }
  },

  6.5. 下拉框
  option: {
    type: 'select',
    disabled: false,
    clear: true,
    multiple: true,
    value: null,
    name: '',
    placeholder: '请选择',
    data: [
      {text: '选项1', value: '选项1', disabled: false},
      {text: '选项2', value: '选项2', disabled: false},
    ],
  },
  event: {
    onChange: (value, selectedValues, plugin) => { }
    onFocus: (event, plugin) => { }
    onBlur: (event, plugin) => { }
    onClear: (plugin) => { }
  },

  6.6. 输入框
  option: {
    type: 'input',
    disabled: false,
    clearable: false,
    value: null,
    placeholder: '请输入',
    name: '',
  },
  event: {
    onChange: (value, plugin) => { }
    onFocus: (event, plugin) => { }
    onBlur: (event, plugin) => { }
    onClear: (plugin) => { }
  },

  6.7. 标签
  option: {
    type: 'label',
    html: "",
  },

  6.8. 图片
  option: {
    type: 'image',
    src: "",
    alt: "",
  },

  6.9. 链接
  option: {
    type: 'link',
    html: "",
  },
  event: {
    onClick: (plugin) => { }
  },

  6.10. 链接组
  option: {
    type: 'link-group',
    data: [
      {html: '上页', disabled: false},
      {html: '下页', disabled: true}
    ],
  },
  event: {
    onClick: (index, plugin) => { }
  },

  6.11. DIV
  option: {
    type: 'container',
    html: html内容
  },

  6.12. 日期选择器
  option: {
    type: 'date',
    formatText: 'yyyy-MM-dd',
    formatValue: 'yyyy-MM-dd',
    readonly: false,
    disabled: false,
    name: '',
    default: '',
  },
  event: {
    onChange: (value, plugin) => { }
    onBlur: (plugin) => { }
    onFocus: (plugin) => { }
  },
},

方法：
1. 修改Vuex Store的值
stateProp：vuex store中的属性，例如screen.govern.showCamera
value：给所有绑定的属性赋值
commit: function(stateProp, value)

2. 修改数据绑定的值
binderName：binder规则的name属性
value：给所有绑定的属性赋值
commitBinder: function(binderName, value)

3. reload: function()
重新加载报表

-->
<template>
  <div class="plugin-container"
       v-if="!style || !style.hidden"
       :class="style.class"
       :style="style.position"
       :id="id + '-container'">
    <slot name="plugin-before"></slot>

    <div v-if="option.type == 'label'"
         class="plugin-label"
         v-html="option.html">
    </div>

    <div v-else-if="option.type == 'container'"
         class="plugin-container"
         v-html="option.html">
    </div>

    <el-button v-else-if="option.type == 'link'"
               type="text"
               :disabled="option.disabled"
               class="plugin-link"
               v-html="option.html"
               @click="onLinkClick">
    </el-button>

    <el-button-group v-else-if="option.type == 'link-group'"
                     class="plugin-link-group">
      <el-button v-for="(button, index) in option.data"
                 :key="index"
                 type="text"
                 v-html="button.html"
                 :disabled="button.disabled"
                 @click="onLinkClick(index)">
      </el-button>
    </el-button-group>

    <img v-else-if="option.type == 'image'"
         :src="option.src"
         :alt="option.alt"
         class="plugin-image">

    <el-input v-else-if="option.type == 'input'"
              :placeholder="option.placeholder || '请输入'"
              v-model="option.value"
              :value="option.value"
              :disabled="option.disabled"
              :clearable="option.clearable"
              :name="option.name"
              class="plugin-input"
              @change="onInputChange"
              @focus="onInputFocus"
              @blur="onInputBlur"
              @clear="onInputClear">
    </el-input>

    <el-button v-else-if="option.type == 'button'"
               :plain="option.style == 'plain'"
               :round="option.style == 'round'"
               :circle="option.style == 'circle'"
               :disabled="option.disabled"
               v-html="button.html"
               class="plugin-button"
               @click="onButtonClick">
    </el-button>

    <el-button-group v-else-if="option.type == 'button-group'"
                     class="plugin-button-group">
      <el-button v-for="(button, index) in option.data"
                 :key="index"
                 v-html="button.html"
                 :disabled="button.disabled"
                 @click="onButtonClick(index)">
      </el-button>
    </el-button-group>

    <el-radio-group v-else-if="option.type == 'radio'"
                    v-model="option.value"
                    class="plugin-radio"
                    @change="onRadioChange">
      <template v-if="option.style == 'button'">
        <el-radio-button v-for="(item, index) in option.data"
                         :key="index"
                         :label="item.value || index"
                         :disabled="item.disabled"
                         :name="item.name">
          <i class="icon iconfont" :class="item.icon"></i><span>{{item.text}}</span>
        </el-radio-button>
      </template>
      <template v-else>
        <el-radio v-for="(item, index) in option.data"
                  :key="index"
                  :label="item.value || index"
                  :disabled="item.disabled"
                  :name="option.name">
          <i class="icon iconfont" :class="item.icon"></i><span>{{item.text}}</span>
        </el-radio>
      </template>
    </el-radio-group>

    <el-checkbox-group v-else-if="option.type == 'check'"
                       class="plugin-checkbox"
                       v-model="option.value">
      <template v-if="option.style == 'button'">
        <el-checkbox-button v-for="(item, index) in option.data"
                            :key="index"
                            :label="item.value || index"
                            :checked="item.checked"
                            :disabled="item.disabled"
                            :name="option.name"
                            @change="onCheckChange($event,index)">
          <i class="icon iconfont" :class="item.icon"></i><span>{{item.text}}</span>
        </el-checkbox-button>
      </template>
      <template v-else>
        <el-checkbox v-for="(item, index) in option.data"
                     :key="index"
                     :label="item.value || index"
                     :checked="item.checked"
                     :disabled="item.disabled"
                     :name="option.name"
                     @change="onCheckChange($event,index)">
          <i class="icon iconfont" :class="item.icon"></i><span>{{item.text}}</span>
        </el-checkbox>
      </template>
    </el-checkbox-group>

    <el-select v-else-if="option.type == 'select'"
               v-model="option.value"
               :disabled="option.disabled"
               :multiple="option.multiple"
               :placeholder="option.placeholder || '请选择'"
               :name="option.name"
               class="plugin-select"
               @change="onSelectChange"
               @focus="onSelectFocus"
               @blur="onSelectBlur"
               @clear="onSelectClear">
      <el-option v-for="item in option.data"
                 :key="item.value"
                 :label="item.text"
                 :value="item.value">
      </el-option>
    </el-select>

    <el-date-picker v-else-if="option.type == 'date'"
                    v-model="option.value"
                    type="date"
                    :format="option.formatText"
                    :value-format="option.formatValue"
                    :readonly="option.readonly"
                    :disabled="option.disabled"
                    :name="option.name"
                    :default-value="option.default"
                    :placeholder="option.placeholder || '选择日期'"
                    @change="onDateChange"
                    @blur="onDateBlur"
                    @focus="onDateFocus">
    </el-date-picker>

    <el-progress v-else-if="option.type == 'progress'"
                 :percentage="option.value"
                 :type="option.style || 'line'"
                 :stroke-width="option.width"
                 :show-text="option.showText || false">
    </el-progress>

    <slot name="plugin-after"></slot>
  </div>
</template>

<script>
  import _ from "lodash"
  import Config from "../../../../config/config";

  export default {
    name: "Plugin",
    props: ['config'],
    data() {
      return {
        id: null,
        style: this.config.style || {class: null, position: null},
        binder: this.config.binder,
        option: this.config.option,
        handler: this.config.handler,
        event: this.config.event,
        proxy: this.config.proxy || {
          remote: false,
          data: null
        },
      }
    },
    created() {
      if (!this.config) {
        throw new Error("Plugin：未声明config属性")
      }
      this.id = this.config.id || "plugin-" + new Date().getTime();
      this.initPosition()
      this.initBinder()
    },
    mounted() {
      this.init()
    },
    beforeDestroy() {
      this.$store.commit(Config.MUTATIONS.SCREEN.UNREGISTER, {
        comp: this
      })
    },
    computed: {},
    methods: {
      /**
       * 处理位置参数
       */
      initPosition() {
        if (this.style.position) {
          let zIndex = this.style.position.zIndex || 0
          this.style.position = _.mapValues(this.style.position, function (o) {
            return o + "px"
          })
          _.set(this.style, 'position.zIndex', zIndex)
          _.set(this.style, 'position.position', 'absolute')
        }
      },
      /**
       * 处理位置参数
       */
      initBinder() {
        for (let key in this.binder) {
          let prop = this.binder[key][0]
          let storeProp = this.binder[key][1]
          this.$store.commit(Config.MUTATIONS.SCREEN.REGISTER, {
            store: storeProp,
            prop: prop,
            comp: this
          })
        }
      },
      /**
       * 初始化组件
       */
      init() {
        this.load()
        this.proxyMethod(this.event, "onInit", [this])
      },
      /**
       * 加载配置
       */
      load() {
        if (!this.proxy || this.proxy.remote === false) {
          this.transformData()
          this.proxyMethod(this.event, "onLoad", [this])
          return;
        }
        this.loadRemoteData();
      },
      /**
       * 重新加载组件
       */
      reload() {
        this.load()
      },
      /**
       * 获取远程数据
       */
      loadRemoteData() {
        if (!this.proxy || !this.proxy.remote) {
          return;
        }
        if (!this.proxy.url) {
          throw new Error("Plugin：未声明获取数据的URL")
        }
        if (!this.proxy.method) {
          this.proxy.method == "POST"
        }
        this.ajax({
          method: this.proxy.method,
          url: this.proxy.url,
          data: this.proxy.param,
        }).then(response => {
          this.proxy.data = response.data
          this.transformData();
          this.proxyMethod(this.event, "onLoad", [this])
        }).catch(error => {
          this.$message.error(error.message)
        });
      },
      /**
       * 将数据转化到插件相应的属性上，并重新加载插件
       */
      transformData() {
        if (!this.handler || !this.handler.transformData) {
          return
        }
        this.proxyMethod(this.handler, "transformData", [this.proxy.data, this.option, this])
      },
      onInputChange(value) {
        this.proxyMethod(this.event, "onChange", [value, this])
      },
      onInputFocus(event) {
        this.proxyMethod(this.event, "onFocus", [event, this])
      },
      onInputBlur(event) {
        this.proxyMethod(this.event, "onBlur", [event, this])
      },
      onInputClear() {
        this.proxyMethod(this.event, "onClear", [this])
      },
      onLinkClick(index) {
        this.proxyMethod(this.event, "onClick", [index, this])
      },
      onButtonClick(index) {
        if (index === undefined) {
          this.proxyMethod(this.event, "onClick", [this])
        } else {
          this.proxyMethod(this.event, "onClick", [index, this])
        }
      },
      onRadioChange(value) {
        this.proxyMethod(this.event, "onChange", [value, this.option, this])
      },
      onCheckChange(value, index) {
        this.proxyMethod(this.event, "onChange", [value, index, this.option, this])
      },
      onSelectChange(value) {
        this.proxyMethod(this.event, "onChange", [value, this.option, this])
      },
      onSelectFocus(event) {
        this.proxyMethod(this.event, "onFocus", [event, this])
      },
      onSelectBlur(event) {
        this.proxyMethod(this.event, "onBlur", [event, this])
      },
      onSelectClear() {
        this.proxyMethod(this.event, "onClear", [this])
      },
      onDateChange(value) {
        this.proxyMethod(this.event, "onChange", [value, this])
      },
      onDateBlur() {
        this.proxyMethod(this.event, "onBlur", [this])
      },
      onDateFocus() {
        this.proxyMethod(this.event, "onFocus", [this])
      },
      /**
       * 修改binder的值
       */
      commitBinder(binderKey, value) {
        this.commit(this.binder[binderKey][1], value)
      },
      /**
       * 修改store的值
       */
      commit(stateProp, value) {
        this.$store.commit(Config.MUTATIONS.SCREEN.MODIFY, {
          store: stateProp,
          value: value
        });
      },
      /**
       * 代理
       */
      proxyMethod(context, eventName, param) {
        if (!context) {
          return
        }
        let func = context[eventName];
        if (func && typeof func == "function") {
          return func.apply(this, param);
        }
      },
      watch: {
        option: {
          deep: true,
          handler: function (val) {
            this.load()
          }
        },
        proxy: {
          deep: true,
          handler: function (val) {
            this.load()
          }
        }
      }
    }
  }
</script>



