<!--
player组件，通过传入:config，生成相应类型的元素
配置项：
{
  1. 样式
  style: {
    // 是否隐藏，默认false
    hidden: false,
    class: 样式class,
    position: {
      left: 0,
      top: 0,
      right: 'auto',
      bottom: 'auto',
      zIndex: 0,
      width: 'auto',
      height: 'auto'
    }
  }
  2. 远端获取数据代理
  proxy: {
    // 通过访问服务端获取数据
    remote: true,
    url: "",
    // POST、GET，默认POST
    method: "",
    // 参数
    param: null,
    // 缓存远端数据
    data: null
  },
  3. 数据绑定
  binder: {
    // name: 绑定规则命名
    // prop: 当前组件的属性路径，进行数据绑定的属性，必须在实例化后存在显式声明，
    // 例如：param.region，必须在config中配置 config: { param: {region: null} }
    // storeProp: Vuex中的属性路径
    // prop与storeProp进行单向数据绑定，当storeProp变化时，prop随之变化，
    // 并会触发当前组件的watch
    name:[prop, storeProp]
  },
  4. 配置
  option: {
    // 是否有播放功能，即播放停止按钮是否显示
    playable: true,
    // 图表，iconfont的图标
    icon: {
      play: 'play',
      stop: 'stop',
      pause: 'pause'
    },
    // 播放间隔，秒
    interval: 1,
    // 提示class
    tooltipClass: '',
  }
  5. 代理函数
  handler: {
    // 数据转换过程，将远端获取的proxy.data转换到option，
    // 或者转换为player对象其他属性的规则
    transformData: function(proxy.data, option, player)
    // 提示的文本格式
    formatTooltip: function(index, data, player)
  },
  6. 事件
  event: {
    // 初始化完成
    onInit: (plugin) { },
    // 加载完成
    onLoad: (plugin) { },
    // 到某个节点触发事件
    onHandlerNode: (index, data, player) { },
    // 播放时事件
    onPlay: (index, data, player) { },
    // 暂停时事件
    onPause: (player) { },
    // 停止时事件
    onStop: (player) { },
    // 重置时事件
    onReset: (player) { },
  },
}

方法：
1. 修改Vuex Store的值
commit: function(stateProp, value)
stateProp：vuex store中的属性，例如screen.govern.showCamera
value：给所有绑定的属性赋值

2. 修改数据绑定的值
commitBinder: function(binderName, value)
binderName：binder规则的name属性
value：给所有绑定的属性赋值

3. reload: function()
重新加载

4. play: function()
播放

5. pause: function()
暂停

6. stop: function()
停止

7. reset: function()
重置

-->
<template>
  <div v-if="!style || !style.hidden"
       class="player-container"
       :class="style.class"
       :style="style.position"
       :id="id + '-container'">
    <slot name="player-before"></slot>
    <!-- 用于管理通用的报表容器样式 -->
    <div :id="id" class="player-ins">
      <div class="stop-area">
        <el-button v-if="option.playable"
                   :disabled="!state.playing"
                   class="stop-button"
                   @click="stop">
          <i class="icon iconfont" :class="'icon-' + option.icon.stop"></i>
        </el-button>
      </div>
      <div class="play-area">
        <el-button v-if="option.playable && !state.playing"
                   class="play-button"
                   @click="play">
          <i class="icon iconfont" :class="'icon-' + option.icon.play"></i>
        </el-button>
      </div>
      <div class="pause-area">
        <el-button v-if="option.playable && state.playing"
                   class="play-button"
                   @click="pause">
          <i class="icon iconfont" :class="'icon-' + option.icon.pause"></i>
        </el-button>
      </div>
      <div class="timeline-area">
        <el-slider v-model="state.index"
                   :min="0"
                   :max="option.data.length - 1"
                   :step="1"
                   :tooltip-class="style.tooltipClass"
                   :format-tooltip="formatTooltip"
                   @change="onNodeClick">
        </el-slider>
      </div>
    </div>
    <slot name="player-after"></slot>
  </div>
</template>

<script>
  import Config from "../../../../config/config"
  import _ from "lodash"

  export default {
    name: "Player",
    props: ['config'],
    data() {
      return {
        id: null,
        style: this.config.style || {class: null, position: null, tooltipClass: null},
        binder: this.config.binder,
        option: this.config.option,
        handler: this.config.handler,
        event: this.config.event,
        proxy: this.config.proxy,
        state: {
          index: 0,
          playing: false,
          interval: (this.config.option.interval || 1) * 1000,
          thread: null,
        },
      }
    },
    created() {
      if (!this.config) {
        throw new Error("Player：未声明config属性")
      }
      // 创建id
      this.id = this.config.id || "player-" + new Date().getTime();
      this.option.data = []
      if (this.option.playable !== false) {
        this.option.playable = true
      }
      this.initIcon();
      this.initPosition()
      this.initBinder()
    },
    mounted() {
      this.init()
    },
    beforeDestroy() {
      // 销毁前注销组件的数据绑定
      this.$store.commit(Config.MUTATIONS.SCREEN.UNREGISTER, {
        comp: this
      })
    },
    methods: {
      /**
       * 播放
       */
      play() {
        this.state.playing = true
        this.proxyMethod(this.event, "onPlay", [this])
        this.state.thread = setInterval(() => {
          this.proxyMethod(this.event, "onHandlerNode", [this.state.index, this.option.data[this.state.index], this])
          this.state.index++;
          if (this.state.index == this.option.data.length) {
            this.stop()
          }
        }, this.state.interval)
      },
      /**
       * 暂停
       */
      pause() {
        this.state.playing = false
        clearInterval(this.state.thread)
        this.proxyMethod(this.event, "onPause", [this.state.index, this.option.data[this.state.index], this])
      },
      /**
       * 停止
       */
      stop() {
        this.state.playing = false
        this.state.index = 0
        clearInterval(this.state.thread)
        this.proxyMethod(this.event, "onStop", [this])
      },
      /**
       * 重置
       */
      reset() {
        this.state.playing = false
        this.state.index = 0
        clearInterval(this.state.thread)
        this.proxyMethod(this.event, "onReset", [this])
      },
      /**
       * 处理节点
       */
      onNodeClick(value) {
        this.state.playing = false
        clearInterval(this.state.thread)
        this.proxyMethod(this.event, "onHandlerNode", [value, this.option.data[value], this])
      },
      /**
       * 提示内容格式
       */
      formatTooltip(value) {
        if (!value) {
          return
        }
        return this.proxyMethod(this.handler, "formatTooltip", [value, this.option.data[value], this]);
      },
      /**
       * 处理图标
       */
      initIcon() {
        let icon = Object.assign({
          play: 'play',
          stop: 'stop',
          pause: 'pause'
        }, this.option.icon)
        this.option.icon = icon
      },
      /**
       * 处理位置参数
       */
      initPosition() {
        if (this.style.position) {
          let zIndex = this.style.position.zIndex || 0
          this.style.position = _.mapValues(this.style.position, function (o) {
            return o + "px"
          })
          _.set(this.style, 'position.zIndex', zIndex)
          _.set(this.style, 'position.position', 'absolute')
        }
      },
      /**
       * 处理位置参数
       */
      initBinder() {
        for (let key in this.binder) {
          let prop = this.binder[key][0]
          let storeProp = this.binder[key][1]
          this.$store.commit(Config.MUTATIONS.SCREEN.REGISTER, {
            store: storeProp,
            prop: prop,
            comp: this
          })
        }
      },
      /**
       * 初始化组件
       */
      init() {
        this.load()
      },
      /**
       * 加载配置
       */
      load() {
        if (this.proxy.remote !== false) {
          this.loadRemoteData();
        } else {
          this.transformData();
          this.proxyMethod(this.event, "onLoad", [this])
        }
      },
      /**
       * 加载服务端数据
       */
      loadRemoteData() {
        if (!this.proxy || !this.proxy.remote) {
          return;
        }
        if (!this.proxy.url) {
          throw new Error("Player：未声明获取数据的URL")
        }
        if (!this.proxy.method) {
          this.proxy.method = "POST"
        }
        this.ajax({
          method: this.proxy.method,
          url: this.proxy.url,
          data: this.proxy.param,
        }).then(response => {
          this.proxy.data = response.data
          this.transformData();
        }).catch(error => {
          this.$message.error(error.message)
        });
      },
      /**
       * 将数据转化到图表相应的属性上，并重新加载图表
       */
      transformData() {
        if (this.handler && this.handler.transformData) {
          this.proxyMethod(this.handler, "transformData", [this.proxy.data, this.option, this]);
        } else {
          this.option.data = this.proxy.data
        }
      },
      /**
       * 代理
       */
      proxyMethod(context, eventName, param) {
        if (!context) {
          return
        }
        let func = context[eventName];
        if (func && typeof func == "function") {
          return func.apply(this, param);
        }
      },
      /**
       * 修改binder的值
       */
      commitBinder(binderKey, value) {
        this.commit(this.binder[binderKey][1], value)
      },
      /**
       * 修改store的值
       */
      commit(stateProp, value) {
        this.$store.commit(Config.MUTATIONS.SCREEN.MODIFY, {
          store: stateProp,
          value: value
        });
      },
      /**
       * 重新加载报表
       */
      reload() {
        this.load()
      },
    },
    watch: {
      option: {
        deep: true,
        handler: function (val) {
          this.load()
        }
      },
      event: {
        deep: true,
        handler: function (val) {

        }
      },
      proxy: {
        deep: true,
        handler: function (val) {
          this.load()
        }
      }
    }
  }
</script>



