import Base from './Base';
import axios from 'axios';

class ApiData extends Base {
  constructor(config) {
    super(config);
  }

  do() {
    /**
     * Promise使AJAX同步执行，避免传入回调函数
     */
    return new Promise((resolve, reject) => {
      axios({
        url: this.config.url,
        method: this.config.method,
        data: this.config.param,
      }).then((response) => {
        resolve(response)
      }).catch((error) => {
        reject(error)
      })
    })
  }
}

export default ApiData;



