<!--

-->
<template>
  <div :style="calcStyle"
       class="chart-ins">
    <div :id="config.id"
         :style="{width:config.style.width + 'px', height: config.style.height + 'px'}">
    </div>
  </div>
</template>

<script>
  import echarts from "echarts"
  import DataProxy from "../../../proxy/DataProxy"
  import Config from "../../../../config/config"

  export default {
    name: "Chart",
    props: ['config'],
    data() {
      return {
        chart: null
      }
    },
    created() {
      this.initStyle();
      this.initBinder();
    },
    mounted() {
      this.init()
    },
    beforeDestroy() {
      this.$store.commit(Config.MUTATIONS.UNREGISTER, {
        comp: this
      })
    },
    computed: {
      /**
       * 计算容器样式
       */
      calcStyle() {
        return {
          width: this.config.style.width + 'px',
          height: this.config.style.height + 'px',
          opacity: this.config.style.opacity,
          display: this.config.style.display,
        }
      }
    },
    methods: {
      /**
       * 初始化组件
       */
      init() {
        this.initIns();
        this.initEvent();
        this.render();
        this.proxyMethod(this.event, "onInit", [this])
      },
      /**
       * 初始化图表实例
       */
      initIns() {
        let dom = document.getElementById(this.config.id);
        this.chart = echarts.init(dom);
      },
      /**
       * 初始化样式，对新组件使用默认样式
       */
      initStyle() {
        this.config.style = this.config.style || {
          top: 30,
          left: 30,
          width: 400,
          height: 300,
          opacity: 1,
          display: 'block',
        }
      },
      /**
       * 处理绑定参数
       */
      initBinder() {
        for (let key in this.binder) {
          let prop = this.binder[key][0]
          let storeProp = this.binder[key][1]
          this.$store.commit(Config.MUTATIONS.REGISTER, {
            store: storeProp,
            prop: prop,
            comp: this
          })
        }
      },
      /**
       * 注册所有传入的事件
       */
      initEvent() {
        if (!this.event) {
          return;
        }
        let me = this;
        for (let eventName in me.event) {
          me.chart.on(eventName, function (params) {
            me.proxyMethod(me.event, eventName, [params, me])
          });
        }
      },
      /**
       * 加载配置
       */
      async render() {
        try {
          let proxy = new DataProxy(this.proxy);
          await proxy.do();
          this.loadData();
        } catch (e) {

        } finally {
          this.loadOption();
          this.proxyMethod(this.event, "onRender", [this])
        }
      },
      /**
       * 加载数据
       */
      loadData() {
        this.$parent.handler();
      },
      /**
       * 加载配置
       */
      loadOption() {
        try {
          this.chart.setOption(this.config.option, true);
        } catch (e) {
        }
      },
      /**
       * 代理
       */
      proxyMethod(context, eventName, param) {
        if (!context) {
          return
        }
        let func = context[eventName];
        if (func && typeof func == "function") {
          return func.apply(this, param);
        }
      },
      /**
       * 修改binder的值
       */
      commitBinder(binderKey, value) {
        this.commit(this.binder[binderKey][1], value)
      },
      /**
       * 修改store的值
       */
      commit(stateProp, value) {
        this.$store.commit(Config.MUTATIONS.MODIFY, {
          store: stateProp,
          value: value
        });
      },
      /**
       * 重新加载报表
       */
      reload() {
        this.render()
      },
      /**
       * chart刷新尺寸
       */
      resize() {
        this.chart.resize({
          height: this.config.style.height,
          width: this.config.style.width,
          silent: true
        });
      }
    },
    watch: {
      "config.style.height": function () {
        this.resize();
      },
      "config.style.width": function () {
        this.resize();
      },
      "config.option": {
        deep: true,
        handler: function (val) {
          this.loadOption()
        }
      },
      "config.event": {
        deep: true,
        handler: function (val) {
          this.initEvent()
        }
      },
      "config.data": {
        deep: true,
        handler: function (val) {
          this.render()
        }
      },
      "config.handler": {
        deep: true,
        handler: function (val) {
          this.loadData()
          this.loadOption()
        }
      }
    }
  }
</script>



