<template>
  <div class="config" @click="stopPropagation($event)">
    <Screen v-show="state.index===undefined || state.index===null"
            :config="config" :state="state"></Screen>
    <Base v-show="state.index!==undefined && state.index!==null"
          :config="config" :state="state"></Base>
  </div>
</template>

<script>
  import Screen from "./config/Screen";
  import Base from "./config/Base";

  export default {
    name: "Config",
    props: ["config", "state"],
    data() {
      return {}
    },
    methods: {
      stopPropagation(e) {
        e.stopPropagation();
      }
    },
    components: {
      'Screen': Screen,
      'Base': Base
    }
  }
</script>

<style scoped>

</style>



