import StaticData from "./entity/StaticData";
import ApiData from "./entity/ApiData";
import _ from "lodash";

class DataProxy {

  constructor(config) {
    if (!config || config.type === undefined || config.type === null) {
      throw new Error("DataSource错误：数据源类型为空。");
    }
    this.config = config;
    switch (this.config.type) {
      case 1:
        this.dataSource = new ApiData(this.config);
        break;
      default:
        this.dataSource = new StaticData(this.config);
        break;
    }
  }

  do() {
    /**
     * Promise使AJAX同步执行，避免传入回调函数
     * 调用时使用 async 和 await 关键字：
     * async initOption() {
     *     let dp = new DataProxy(config)
     *     let value = await dp.do();
     *     // do something
     * }
     */
    return new Promise((resolve, reject) => {
      this.dataSource.do()
        .then((response) => {
          let data = response.data || response
          if (this.config.prop) {
            this.config.data = _.get(data, this.config.prop);
          } else {
            this.config.data = data
          }
          resolve(this.config.data);
        })
        .catch((error) => {
          reject(error);
        })
    })
  }
}

export default DataProxy;



