import Vue from 'vue'
import Router from 'vue-router'
import Viewport from '@/views/Viewport'
import ScreenDesign from '@/views/screen/Design'

Vue.use(Router)

export default new Router({
  /*mode: 'history',*/
  routes: [{
    path: '/viewport',
    name: 'Viewport',
    component: Viewport
  }, {
    path: '/',
    name: 'ScreenDesign',
    component: ScreenDesign
  }]
})



