<template>
  <el-collapse value="0" accordion>
    <!-- 基础属性 -->
    <el-collapse-item title="基础属性" name="0">
      <el-row>
        <el-col :span="8" class="tag">图表尺寸</el-col>
        <el-col :span="8">
          <el-input-number v-model="component.config.style.width"
                           size="mini"
                           controls-position="right"
                           :min="0">
          </el-input-number>
          <div class="description">宽度</div>
        </el-col>
        <el-col :span="8">
          <el-input-number v-model="component.config.style.height"
                           size="mini"
                           controls-position="right"
                           :min="0">
          </el-input-number>
          <div class="description">高度</div>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="8" class="tag">图表位置</el-col>
        <el-col :span="8">
          <el-input-number v-model="component.config.style.left"
                           size="mini"
                           controls-position="right">
          </el-input-number>
          <div class="description">横坐标</div>
        </el-col>
        <el-col :span="8">
          <el-input-number v-model="component.config.style.top"
                           size="mini"
                           controls-position="right">
          </el-input-number>
          <div class="description">纵坐标</div>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="8" class="tag">其他</el-col>
        <el-col :span="8">
          <el-input-number v-model="component.config.style.opacity"
                           size="mini"
                           controls-position="right" :min="0" :max="1" :step="0.05">
          </el-input-number>
          <div class="description">透明度</div>
        </el-col>
        <el-col :span="8">
        </el-col>
      </el-row>
    </el-collapse-item>
    <!-- 样式 -->
    <el-collapse-item title="样式" name="1">
      <el-collapse value="0">
        <!-- 柱子样式 -->
        <el-collapse-item title="柱子样式" name="0">
          <el-row>
            <el-col :span="8" class="tag">柱子宽度</el-col>
            <el-col :span="8">
              <el-input-number :value="getBarWidth"
                               @change="setBarWidth"
                               size="mini"
                               controls-position="right" :min="0" :max="1" :step="0.1">
              </el-input-number>
            </el-col>
            <el-col :span="8">
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8" class="tag">柱子背景色</el-col>
            <el-col :span="4">
              <el-color-picker v-model="component.config.option.series[1].itemStyle.normal.color"
                               size="mini" show-alpha></el-color-picker>
            </el-col>
            <el-col :span="4">
              <el-button
                @click="resetValue('component.config.option.series[1].itemStyle.normal.color', 'barBgColor')"
                type="text" size="mini">重置
              </el-button>
            </el-col>
          </el-row>
        </el-collapse-item>
        <!-- 值标签 -->
        <el-collapse-item title="值标签" name="1">
          <el-row>
            <el-col :span="8" class="tag">位置</el-col>
            <el-col :span="16">
              <el-select v-model="component.config.option.series[0].label.normal.position"
                         placeholder="请选择"
                         size="mini">
                <el-option v-for="item in labelPositions"
                           :key="item.value"
                           :label="item.label"
                           :value="item.value">
                </el-option>
              </el-select>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8" class="tag">字号</el-col>
            <el-col :span="8">
              <el-input-number v-model="component.config.option.series[0].label.normal.textStyle.fontSize"
                               size="mini"
                               controls-position="right"
                               :min="12">
              </el-input-number>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8" class="tag">颜色</el-col>
            <el-col :span="4">
              <el-color-picker v-model="component.config.option.series[0].label.normal.textStyle.color"
                               size="mini" show-alpha></el-color-picker>
            </el-col>
            <el-col :span="4">
              <el-button
                @click="resetValue('component.config.option.series[0].label.normal.textStyle.color', 'labelColor')"
                type="text" size="mini">重置
              </el-button>
            </el-col>
          </el-row>
        </el-collapse-item>
      </el-collapse>

    </el-collapse-item>

    <!-- x轴 -->
    <el-collapse-item title="x轴" name="2">
      <el-collapse value="0">
        <!-- 标签 -->
        <el-collapse-item title="标签" name="0">
          <el-row>
            <el-col :span="8" class="tag">文本</el-col>
            <el-col :span="8">
              <el-checkbox v-model="component.config.option.xAxis[0].axisLabel.show">
                {{component.config.option.xAxis[0].axisLabel.show ? "显示" : "隐藏"}}
              </el-checkbox>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8" class="tag">字号</el-col>
            <el-col :span="8">
              <el-input-number v-model="component.config.option.xAxis[0].axisLabel.fontSize"
                               size="mini"
                               controls-position="right"
                               :min="12">
              </el-input-number>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8" class="tag">颜色</el-col>
            <el-col :span="4">
              <el-color-picker v-model="component.config.option.xAxis[0].axisLabel.color"
                               size="mini" show-alpha></el-color-picker>
            </el-col>
            <el-col :span="4">
              <el-button @click="resetValue('component.config.option.xAxis[0].axisLabel.color', 'xAxisLabelColor')"
                         type="text" size="mini">重置
              </el-button>
            </el-col>
          </el-row>
        </el-collapse-item>
        <!-- 轴线 -->
        <el-collapse-item title="轴线" name="1">
          <el-row>
            <el-col :span="8" class="tag">颜色</el-col>
            <el-col :span="4">
              <el-color-picker v-model="component.config.option.xAxis[0].axisLine.lineStyle.color"
                               size="mini" show-alpha></el-color-picker>
            </el-col>
            <el-col :span="4">
              <el-button
                @click="resetValue('component.config.option.xAxis[0].axisLine.lineStyle.color', 'xAxisLineColor')"
                type="text" size="mini">重置
              </el-button>
            </el-col>
          </el-row>
        </el-collapse-item>
      </el-collapse>
    </el-collapse-item>

    <!-- y轴 -->
    <el-collapse-item title="y轴" name="3">
      <el-row>
        <el-col :span="8" class="tag">标签字色</el-col>
        <el-col :span="4">
          <el-color-picker v-model="component.config.option.yAxis[0].axisLabel.color"
                           size="mini" show-alpha></el-color-picker>
        </el-col>
        <el-col :span="4">
          <el-button @click="resetValue('component.config.option.yAxis[0].axisLabel.color', 'yAxisLabelColor')"
                     type="text" size="mini">重置
          </el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="8" class="tag">轴线色</el-col>
        <el-col :span="4">
          <el-color-picker v-model="component.config.option.yAxis[0].axisLine.lineStyle.color"
                           size="mini" show-alpha></el-color-picker>
        </el-col>
        <el-col :span="4">
          <el-button
            @click="resetValue('component.config.option.yAxis[0].axisLine.lineStyle.color', 'yAxisLineColor')"
            type="text" size="mini">重置
          </el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="8" class="tag">分隔线</el-col>
        <el-col :span="8">
          <el-checkbox v-model="component.config.option.yAxis[0].splitLine.show">
            {{component.config.option.yAxis[0].splitLine.show ? "显示" : "隐藏"}}
          </el-checkbox>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="8" class="tag">刻度</el-col>
        <el-col :span="8">
          <el-checkbox v-model="component.config.option.yAxis[0].axisLabel.show">
            {{component.config.option.yAxis[0].axisLabel.show ? "显示" : "隐藏"}}
          </el-checkbox>
        </el-col>
      </el-row>
    </el-collapse-item>

    <!-- 系列 -->
    <el-collapse-item title="数据系列" name="4">
      <el-row>
        <el-col :span="8" class="tag">系列颜色</el-col>
        <el-col :span="16">
          <el-row>
            <el-select v-model="barFill.value"
                       placeholder="请选择"
                       size="mini"
                       @change="changeBarFill">
              <el-option v-for="item in barFill.option"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
          </el-row>
          <el-row v-show="barFill.value===0">
            <el-col :span="6">
              <el-color-picker v-model="barFill.single"
                               size="mini" show-alpha
                               @change="changeBarFill"></el-color-picker>
            </el-col>
            <el-col :span="12" class="tag">填充色</el-col>
          </el-row>
          <el-row v-show="barFill.value===1">
            <el-col :span="6">
              <el-color-picker v-model="barFill.gradient.colorStops[0].color"
                               size="mini" show-alpha
                               @change="changeBarFill"></el-color-picker>
            </el-col>
            <el-col :span="12" class="tag">起始色</el-col>
          </el-row>
          <el-row v-show="barFill.value===1">
            <el-col :span="6">
              <el-color-picker v-model="barFill.gradient.colorStops[1].color"
                               size="mini" show-alpha
                               @change="changeBarFill"></el-color-picker>
            </el-col>
            <el-col :span="12" class="tag">终止色</el-col>
          </el-row>
        </el-col>
      </el-row>
    </el-collapse-item>
  </el-collapse>
</template>

<script>
  import _ from 'lodash'
  import Data from '../Data'

  export default {
    name: "BaseBar",
    props: ["component"],
    data() {
      return {
        default: {
          // 柱子颜色
          barColor: null,
          // 柱子背景色
          barBgColor: null,
          // 柱子上的标签字色
          labelColor: null,
          // x轴标签字色
          xAxisLabelColor: null,
          // x轴线颜色
          xAxisLineColor: null,
          // y轴标签字色
          yAxisLabelColor: null,
          // x轴线颜色
          yAxisLineColor: null,
        },
        // 柱子填充状态和填充色
        barFill: {
          value: 0,
          single: null,
          gradient: {
            x: 0,
            y: 0,
            x2: 0,
            y2: 1,
            type: "linear",
            global: false,
            colorStops: [
              {offset: 0, color: "#fff"},
              {offset: 1, color: "#000"}
            ]
          },
          option: [
            {label: "纯色", value: 0},
            {label: "渐变色", value: 1},
          ],
        },
        // 标签位置选项
        labelPositions: [
          {label: "顶部", value: "top"},
          {label: "左侧", value: "left"},
          {label: "右侧", value: "right"},
          {label: "底部", value: "bottom"},
          {label: "内部", value: "inside"},
          {label: "左内侧", value: "insideLeft"},
          {label: "右内侧", value: "insideRight"},
          {label: "内侧顶部", value: "insideTop"},
          {label: "内侧底部", value: "insideBottom"},
          {label: "左内侧顶部", value: "insideTopLeft"},
          {label: "左内侧底部", value: "insideBottomLeft"},
          {label: "右内侧顶部", value: "insideTopRight"},
          {label: "右内侧底部", value: "insideBottomRight"},
        ]
      }
    },
    created() {
      // 缓存部分颜色的默认值，方便重置颜色时候恢复
      this.default = {
        barColor: this.component.config.option.series[0].itemStyle.normal.color,
        barBgColor: this.component.config.option.series[1].itemStyle.normal.color,
        labelColor: this.component.config.option.series[0].label.normal.textStyle.color,
        xAxisLabelColor: this.component.config.option.xAxis[0].axisLabel.color,
        xAxisLineColor: this.component.config.option.xAxis[0].axisLine.lineStyle.color,
        yAxisLabelColor: this.component.config.option.yAxis[0].axisLabel.color,
        yAxisLineColor: this.component.config.option.yAxis[0].axisLine.lineStyle.color,
      }

      // 初始化柱子颜色，分情况讨论纯色和渐变色
      if (typeof this.default.barColor === "string") {
        this.barFill.value = 0;
        this.barFill.single = this.default.barColor;
      } else {
        this.barFill.value = 1;
        this.barFill.gradient = _.merge(this.barFill.gradient, this.default.barColor);
      }
    },
    computed: {
      /**
       * 计算柱子宽度
       */
      getBarWidth() {
        return this.percentToNumber(this.component.config.option.series[0].barWidth)
      },
    },
    methods: {
      /**
       * 修改柱子填充色，0-纯色 / 1-渐变
       */
      changeBarFill() {
        if (this.barFill.value === 1) {
          this.component.config.option.series[0].itemStyle.normal.color = this.barFill.gradient
        } else {
          this.component.config.option.series[0].itemStyle.normal.color = this.barFill.single
        }
      },
      /**
       * 重置值
       */
      resetValue(prop, defaultProp) {
        _.set(this, prop, _.get(this.default, defaultProp))
      },
      /**
       * 设置柱子宽度
       */
      setBarWidth(val) {
        this.component.config.option.series[0].barWidth = this.numberToPercent(val)
      },
      /**
       * 百分比转数字
       * @param pct
       * @returns {number}
       */
      percentToNumber(pct) {
        if (!pct) {
          return 0;
        }
        return parseInt(pct.substring(0, pct.length - 1)) / 100;
      },
      /**
       * 数字转百分比
       * @param val
       * @returns {string}
       */
      numberToPercent(val) {
        if (!val) {
          val = 0;
        }
        return val * 100 + "%";
      }
    },
    components: {
      'Data': Data
    },
  }
</script>



