<template>
  <div class="paint">
    <div :style="{width: (config.base.size.width + 60) + 'px',
                height: (config.base.size.height + 60) + 'px'}"
         class="bg-panel">
      <div :style="{width: config.base.size.width + 'px',
                  height: config.base.size.height + 'px',
                  backgroundColor: config.base.backgroundColor,
                  backgroundImage: config.base.backgroundImage ? 'url(' + config.base.backgroundImage + ')' : 'none',
                  backgroundSize: calcBgImageSize
                  }"
           stylle="background-repeat:no-repeat;"
           class="canvas-panel">

        <template v-for="(cmp, index) in config.components">
          <div :style="calcStyle(index)"
               :class="state.index === index ? 'active' : ''"
               class="component-box"
               @click="activeBox($event, index)"
               @mousedown="dragBox($event, index)">
            <component :is="loader[cmp.id].default"
                       :config="cmp.config"></component>

          </div>
        </template>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: "Paint",
    props: ["config", "state"],
    data() {
      return {
        // 动态加载组件的存储
        loader: {},
        // 当前拖拽的组件索引
        draggingIndex: null,
        // 位移参照基准点
        originalPosition: {x: null, y: null}
      }
    },
    created() {
      this.loadComponent()
    },
    computed: {
      calcBgImageSize() {
        switch (this.config.base.scaleMode) {
          case 1:
            return 'auto 100%';
          case 2:
            return '100% 100%';
          default:
            return '100% auto';
        }
      }
    },
    methods: {
      activeBox(e, index) {
        this.state.index = index;
        e.stopPropagation()
      },
      dragBox(e, index) {
        if (index !== this.state.index) {
          return;
        }
        // 当前拖拽的组件索引
        this.draggingIndex = index;
        // 获取位移的参照原点
        this.originalPosition.x = e.screenX;
        this.originalPosition.y = e.screenY;
        // 增加鼠标事件
        window.addEventListener('mousemove', this.moveBox);
        window.addEventListener('mouseup', this.endDrag);
      },
      moveBox(e) {
        // 获取偏移量
        let offsetX = e.screenX - this.originalPosition.x;
        let offsetY = e.screenY - this.originalPosition.y;
        // 将参照原点更新为移动后的坐标，作为下一次移动的参照点
        this.originalPosition.x = e.screenX;
        this.originalPosition.y = e.screenY;
        // 获取组件的样式
        let style = this.config.components[this.state.index].config.style;
        // 计算组件移动后的坐标，并修改组件的属性
        let x = style.left + offsetX;
        let y = style.top + offsetY;
        style.left = x;
        style.top = y;
      },
      endDrag() {
        this.draggingIndex = null;
        window.removeEventListener('mousemove', this.moveBox);
        window.removeEventListener('mouseup', this.endDrag);
      },
      loadComponent() {
        if (!this.config.components || !this.config.components.length) {
          return;
        }
        for (let i = 0; i < this.config.components.length; i++) {
          let comp = this.config.components[i]
          if (!comp || !comp.name || this.loader[comp.id]) {
            continue;
          }
          let path = "components/screen/layer/" + comp.category + "/" + comp.name;
          this.loader[comp.id] = (require("../../" + path + ".vue"));
        }
      },
      /**
       * 计算容器样式
       */
      calcStyle(index) {
        let style = this.config.components[index].config.style
        return {
          zIndex: this.state.index === index ? 1 : 'auto',
          top: style && style.top ? style.top + 'px' : 0,
          left: style && style.left ? style.left + 'px' : 0,
        }
      },
    },
    watch: {
      "config.components": {
        immediate: true,
        handler: function (val, oldVal) {
          this.loadComponent();
        }
      }
    }
  }
</script>

<style scoped>

</style>



