<template>
  <div class="menu">
    <template v-for="(node, i) in $store.state.screen.design.menu">
      <div class="node"
           @mouseover="node.show = true"
           @mouseleave="node.show = false">
        <el-button type="primary"
                   size="mini"
                   circle>
          <i :class="'icon-' + node.icon" class="icon iconfont"></i>
        </el-button>
        <el-tabs v-show="node.show"
                 type="border-card"
                 tab-position="left">
          <template v-for="(category, j) in node.categories">
            <el-tab-pane>
              <span slot="label">
                <i :class="'icon-' + category.icon" class="icon iconfont"></i>
              </span>
              <template v-for="(item, k) in category.items">
                <el-button type="primary"
                           @click="addComponent(i, j, k)">{{item.text}}
                </el-button>
              </template>
            </el-tab-pane>
          </template>
        </el-tabs>
      </div>
    </template>
  </div>
</template>

<script>
  export default {
    name: "Menu",
    props: ["config", "state"],
    data() {
      return {
        showPanel: false,
      }
    },
    methods: {
      /**
       * 添加一个组件
       * @param i
       * @param j
       * @param k
       */
      addComponent(i, j, k) {
        let item = this.$store.state.screen.design.menu[i].categories[j].items[k]
        if (!this.config.components) {
          this.config.components = []
        }
        this.config.components.push({
          text: item.text,
          category: item.category,
          name: item.name,
          config: {
            // id
            id: "cmp-" + new Date().getTime(),
            // 样式
            style: null,
            // 功能配置
            option: null,
            // 数据代理
            proxy: null,
            // 状态存储
            store: null,
            // 状态绑定规则
            binder: null,
            // 数据转换为配置
            handler: null,
            // 事件
            event: null
          }
        })
      }
    }
  }
</script>

<style scoped>

</style>



